import './App.css';
import * as React from 'react';
import { Redirect, RouteComponentProps } from 'react-router';
// @ts-ignore
import * as cookie from 'react-cookies';
import { API_URL } from './Api';

interface State {
    loginError: string | null;
    authenticated: boolean;
    email?: string;
    password?: string;
}

const LOGIN_URL = `${API_URL}/login`;

class LoginPage extends React.Component<RouteComponentProps, State> {
    public state: State = {
        loginError: null,
        authenticated: cookie.load('token') != null
    };

    public render() {
        return this.state.authenticated ? (<Redirect to="/" />) : (
            <div className="App">
                <header className="App-header"/>

                <div style={styles.containerStyle}>
                    <div>GrayVyne</div>
                    <div>Log into Admin portal</div>

                    <div style={styles.loginForm}>
                        <form style={{width: 400}}>
                            <div style={styles.formField}>
                                <input onChange={e => this.setState({email: e.target.value})} style={styles.inputStyle} type="text"  placeholder="Username" />
                            </div>
                            <div style={styles.formField}>
                                <input onChange={e => this.setState({password: e.target.value})} style={styles.inputStyle} type="password" placeholder="Password"/>
                            </div>
                            <div style={styles.formField}>
                                <button style={styles.submitBtn} type="button" onClick={e => this.handleLogin()}>Login</button>
                            </div>
                        </form>
                    </div>

                    {this.state.loginError && (<div style={styles.errorBlock}>{this.state.loginError}</div>)}
                </div>
            </div>
        );
    }

    private handleLogin = () => {
        const creds = {
            email: this.state.email,
            password: this.state.password
        };

        const requestInit = {
            method: 'POST',
            body: JSON.stringify(creds),
            headers: {
                'Content-Type': 'application/json'
            }
        };

        fetch(LOGIN_URL, requestInit)
            .then(resp => resp.json())
            .then(body => {
                if (body.token) {
                    cookie.save('token', body.token, {path: '/'});
                    this.setState({
                        authenticated: true
                    });
                } else {
                    this.setState({
                        loginError: body.message
                    });
                }
            });
    }
}

namespace styles {
    export const containerStyle: React.CSSProperties = {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        marginTop: '2rem'
    };

    export const loginForm: React.CSSProperties = {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '25px 25px 0'
        // margin: '20px 20px 0'
    };

    export const inputStyle: React.CSSProperties = {
        width: '100%',
        padding: '1rem'
    };

    export const formField: React.CSSProperties = {
        marginBottom: '2rem'
    };

    export const submitBtn: React.CSSProperties = {
        padding: '15px',
        width: 100
    };

    export const errorBlock: React.CSSProperties = {
        color: 'red'
    };
}

export default LoginPage;


