import LoginPage from './LoginPage';
import ImportPage from './ImportPage';
import * as React from 'react';
import { Redirect, RedirectProps, Route, RouteProps, Router, Switch } from 'react-router';
import { createBrowserHistory } from 'history';
// @ts-ignore
import * as cookie from 'react-cookies';


interface Props extends Partial<RouteProps>, Partial<RedirectProps> {
    to: string;
    redirectTo: string;
    Component: React.ComponentType<any>;
    path?: string;
}

const isAuthenticated = () => cookie.load('token') != null;

const PrivateRoute: React.FunctionComponent<Props> = ({to, redirectTo, Component, exact}) => (
    <Route exact={exact} path={to}
           render={props => (isAuthenticated() ? <Component {...props} /> : <Redirect to={redirectTo}/>)}/>
);

class App extends React.Component {
    private history = createBrowserHistory();

    public render(): JSX.Element {
        return (
            <div>
                <Router history={this.history}>
                    <Switch>
                        <Route path='/login' component={LoginPage} />
                        <PrivateRoute exact={true} to="/" redirectTo="/login" Component={ImportPage} />
                    </Switch>
                </Router>
            </div>
        );
    }
}

export default App;



