import * as React from 'react';
import './App.css';
// @ts-ignore
import * as cookie from 'react-cookies';
import { API_URL } from './Api';

const API_BASE_PATH = `${API_URL}/import`;
const SUCCESS_RESPONSE_MESSAGE = "ok";
const AUTH_ERROR_CODE = 'auth/argument-error';
const EXPIRED_TOKEN_MSG = 'Token expired, please refresh the application';
interface UploadResponse {
    status: string;
}

interface ResponseError {
    code: string;
    message: string;
}

interface State {
    errorMessage: string | undefined;
    successMessage: string | undefined;
}

namespace styles {
    export const errorMessageContainer: React.CSSProperties = { backgroundColor: 'red' };
    export const successMessageContainer: React.CSSProperties = { backgroundColor: 'green' };
}

class ImportPage extends React.Component<{}, State> {

    public state = { errorMessage: undefined, successMessage: undefined };

    private peopleInput: HTMLInputElement | null = null;
    private classesInput: HTMLInputElement | null = null;
    private schoolsInput: HTMLInputElement | null = null;

    public render() {
        return (
            <div className="App">
                <header className="App-header" />
                <form onSubmit={this.handleSubmitFiles}>
                    <div>
                        <p>People</p>
                        <input type="file" ref={element => (this.peopleInput = element)} />
                        <p>Classes</p>
                        <input type="file" ref={element => (this.classesInput = element)} />
                        <p>Schools</p>
                        <input type="file" ref={element => (this.schoolsInput = element)} />
                    </div>
                    <button type="submit">
                        <p>Send!</p>
                    </button>
                    {undefined !== this.state.errorMessage && (
                        <div style={styles.errorMessageContainer}>
                            <p>{this.state.errorMessage}</p>
                            <button onClick={this.handleClearError}>Clear</button>
                        </div>
                    )}
                    {undefined !== this.state.successMessage && (
                        <div style={styles.successMessageContainer}>
                            <p>{this.state.successMessage}</p>
                            <button onClick={this.handleClearMessage}>Clear</button>
                        </div>
                    )}
                </form>
            </div>
        );
    }

    private handleSubmitFiles: React.FormEventHandler<HTMLFormElement> = async event => {
        event.preventDefault();
        try {
            await this.uploadFiles();
            this.setState({ successMessage: 'Successfully imported data', errorMessage: undefined });
        } catch (error) {
            console.error(error);
            this.setState({ errorMessage: error.message });
        }
    };

    private handleClearError = (): void => this.setState({ errorMessage: undefined });

    private handleClearMessage = (): void => this.setState({ successMessage: undefined });

    private async uploadFiles(): Promise<void> {
        if (null == this.peopleInput || null == this.classesInput || null == this.schoolsInput) {
            throw new Error('Not all inputs are referenced');
        }
        if (
            null == this.peopleInput.files ||
            null == this.classesInput.files ||
            null == this.schoolsInput.files ||
            this.peopleInput.files.length !== 1 ||
            this.classesInput.files.length !== 1 ||
            this.schoolsInput.files.length !== 1
        ) {
            throw new Error('All files must be provided');
        }

        const response = await uploadFileToServer([
            this.schoolsInput.files[0],
            this.classesInput.files[0],
            this.peopleInput.files[0]
        ]);

        if (!isSuccessResponse(response)) {
            throw new Error(getErrorMessage(response));
        }
    }

}

export default ImportPage;

function getErrorMessage(response: any) {
    let errorMessage;
    if (response.code && response.code === AUTH_ERROR_CODE) {
        errorMessage = EXPIRED_TOKEN_MSG;
    }
    else {
        errorMessage = response.error.message;
    }
    return errorMessage;
}
function isSuccessResponse(resp: any): resp is UploadResponse {
    return resp.status !== null && resp.status === SUCCESS_RESPONSE_MESSAGE;
}

function uploadFileToServer(files: File[]): Promise<UploadResponse | ResponseError> {
    const body = new FormData();
    files.forEach(file => {
        body.append('files[]', file);
    });

    return fetch(API_BASE_PATH, {
        body, method: 'POST', headers: {
            'Authorization': cookie.load('token')
        }
    })
        .then(response => response.json());
}


